import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			pageName:'越限监测点详情',
			radioFlag:0,
			lineName:'',
			showTime:'',
			lineIndex:'',
			steadyTargetList:[],
			timeID:'',
			errorFlag:0
		}
	},
	methods: {
		steadyTarget(){
			var that = this;
			uni.request({
				url: that.serverUrl+'/steadymsg/steadyTarget',
				method: 'POST',
				data: {
					lineIndex: that.lineIndex,
					timeID:that.timeID
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify: false,
				success: (res) => {
					uni.hideLoading();
					if (res.data.resultCode == '10000') {
						that.steadyTargetList = eval(res.data.data);
					} else {
						that.errorFlag = 1
					}
				},
				fail: (re) => {
					that.errorFlag = 1
				}
			});
		},
		jumpToSteadyIndex(typeCode,typeName){
			var that = this;
			uni.navigateTo({
				url:'../steadyIndex/steadyIndex?typeCode='+typeCode+"&lineIndex="+that.lineIndex+"&timeID="+that.timeID+"&typeName="+typeName
			})
		},
		retry(){
			this.errorFlag=0;
			this.steadyTarget();
		}
	},
	onLoad(e) {
		uni.showLoading({
			mask:true,
			});
		this.showTime=e.showTime;
		this.lineName = e.lineName;
		this.lineIndex=e.lineIndex;
		this.timeID=e.timeID;
	},
	components:{
		back
	}
}
